.shop {
    max-width: 1140px;
    margin: auto;
    padding: 1rem;

    .form select {
        width: 25%;
        height: 5vh;
        gap: 2rem;
        border-radius: 5px;
        cursor: pointer;
        margin: auto;
        outline: none;
    }

    .form select option {
        font-size: 15px;
        font-weight: bold;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        letter-spacing: 0.2px;
        gap: 2rem;
    }

    form {
        padding: 1rem;
        margin: auto;
        flex-direction: column;
        display: flex;
        gap: 2rem;
        border: none;
    }

    form input {
        width: 20%;
        height: 5vh;
        outline: none;
        border-radius: 5px;
        cursor: auto;
    }

    form button {
        width: 25%;
        height: 5vh;
        border: none;
        border-radius: 8px;
        background-color: #007bff;
        color: white;
        cursor: pointer;
    }
}

@media (max-width: 900px) {
    .form input {
        width: 33%;
    }
}