.header {
    box-shadow: 0 0 6px gray;

    &.dark {
        background-color: black;
        color: white;
    }

    header {
        max-width: 1400px;
        padding: 1rem;
       
        gap: 2rem;
        margin: auto;
        // width: 100%;
        transition-duration: 0.6s;



        nav {
            display: flex;
            padding: 20px 40px;
            justify-content: space-between;
            align-items: center;
            gap: 2rem;


            ul {
                display: flex;
                justify-content: space-between;

                // &.active {
                //     display: flex;
                // }



                .Logo {
                    width: 80px;
                    margin-left: 60px;
                }

                h1 {
                    font-size: 20px;
                    width: 40%;
                    margin-left: 80px;
                    margin-top: -70px;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                }


                li {
                    display: flex;
                    list-style: none;
                    margin-left: 30px;


                    a {
                        font-size: 20px;
                        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                        margin-left: 12px;
                        text-decoration: none;
                        color: black;
                        transition-duration: 0.6s;

                        &.dark {
                            color: white;
                        }
                    }

                }

                button {
                    width: 80px;
                    height: 30px;
                    border-radius: 8px;
                    border: none;
                    background-color: blue;
                    color: white;
                    cursor: pointer;
                }

            }

        }

        .hamburger-2 {
            display: none;

            &.active{
                display: block;
            }
        }  
       
    }

    .hamburger {
        display: none;
        font-size: 30px;
        float: right;
        line-height: 60px;
        margin-right: 40px;
        cursor: pointer;
        flex-wrap: wrap;
    }
    
    @media (max-width: 1024px) {
        header .hamburger {
              display: flex;
         }
         header li a{
             display: none;
         }
     }
  
  
}
@media(max-width: 700px){
    .header{
        box-shadow: none;
    }
    .header ul h1{
       display: none;
      }
   
 }
 @media (max-width: 800px){
    .header header button{
        display: none;
    }
 }
