.cardwrapper {
    max-width: 1140px;
    margin: auto;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    // margin-bottom: 50px;
    padding: 2rem 1rem;

    &.dark {
        background-color: black;
        color: white;
    }

    .card {
        width: 22%;
        margin: auto;
        gap: 1rem;
        border: 1px solid #ccc;
        border-radius: 8px;
        padding: 10px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        cursor: pointer;
        flex-wrap: wrap;

        h2{
            margin: auto;
            font-size: 18px;
            
        }

        &.dark {
            background-color: black;
            color: white;
        }

        img {
            width: 100%;
            height: 250px;
        }
    }
    .card:hover {
        opacity: 50%;
    }
    @media (max-width: 900px) {
        .card {
            width: 33%; 
            margin: auto;
        }
    }
    
    @media (max-width: 600px) {
        .card {
            width: 51%; 
            margin: auto;
        }
    }
    
    @media (max-width: 400px) {
        .card {
            width: 100%; 
            margin: auto;
        }
    }
}

.text {
    display: flex;
    gap: 2rem;
    text-align: center;
    cursor: pointer;

    h1{
       color: rgba(31, 31, 154, 0.849);
       font-size: 30px;
       margin-left: 180px;
       font-weight: bold;
       font-weight: 15px;
       margin-top: 3px;
       letter-spacing: 0.2px;
       font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    }

    h2{
        font-size: 28px;
        margin-top: 3px;
        opacity: 50%;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    }
  
    @media(max-width: 500px){
        .text{
            width: 100%;
            text-align: center;
        }
    }
   
}