.login {
    padding: 5rem;
    margin: auto;
    width: 100%;


    form {
        max-width: 600px; 
        margin: auto;
        outline: none;

        span{
            text-align: center;
            color: red;
            font-size: 18px;
        }
        h1{
            color: black;
            font-size: 20px;
            gap: 1rem;
        }

        input {
            width: 100%; 
            padding: 10px;
            border-radius: 5px;
            font-size: 16px;
            margin-bottom: 10px; 
            // outline: none;
            border: none;

        }
    }

    .text {
        text-align: center;
        margin-bottom: 20px;

        h2{
           margin: auto;
           color: black;
           font-size: 30px;
           font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        }
    }

    button {
        width: 100%; 
        text-align: center;
        cursor: pointer;
        background-color: #007bff;
        color: white;
        border: none;
        height: 50px;
    }
}